import BaseCrudStateRepository from "../../../../../core/state/base/BaseCrudStateRepository";

export default class ModalRepository extends BaseCrudStateRepository {

    get initialState() {
        return {
            title: null,
            body: null,
            footer: null,
            content: null,
            onConfirm: null,
            onHide: null,
            isVisible: false,
        };
    }

    get reducerPrefix() {
        return 'modalItem';
    }

    show(data) {
        this.setValue(data);
        this.setValue({
            isVisible: true
        });
    }

    hide() {
        this.setValue({
            isVisible: false
        });
        this.resetValue();
    }
}
