import BaseStateRepository from "./BaseStateRepository";

export default class BaseCrudStateRepository extends BaseStateRepository {

    protected _initialState = {
        dataProvider: null,
        entity: null,
    };

    setDataProvider(dataProvider) {
        this.setValue({
            dataProvider
        });
    }

    setEntity(entity) {
        this.setValue({
            entity
        });
    }
}
