import container from "../../../../core/container/singletons/container";
import ConfirmService from "../services/ConfirmService";
import ModalService from "../services/ModalService";

class ModalFacade {

    private confirmService: ConfirmService;
    private modalService: ModalService;

    constructor(confirmService: ConfirmService, modalService: ModalService) {
        this.confirmService = confirmService;
        this.modalService = modalService;
    }

    showContent(title, content) {
        this.showByEntity({
            title,
            content,
        });
    }

    showByEntity(entity) {
        this.modalService.showByEntity(entity);
    }

    showInfo(title, body) {
        let modalEntity = {
            title: title,
            body: body,
            size: 'lg',
        };
        this.showByEntity(modalEntity);
    }

    showConfirm(title, body, onConfirm = null, onHide = null) {
        this.confirmService.show(title, body, onConfirm, onHide);
    }

    showDeleteConfirm(title, onConfirm, onHide = null) {
        this.showConfirm("Delete confirm", 'Are you sure Delete "' + title + '"?', onConfirm, onHide);
    }

    close() {
        this.modalService.close();
    }
}

let confirmService: ConfirmService = container.get('modal.services.confirm');
let modalService: ModalService = container.get('modal.services.modal');

// eslint-disable-next-line
export default new ModalFacade(confirmService, modalService);
