import React from 'react';
import CopyrightHelper from "../../domain/helpers/CopyrightHelper";

export default function CopyrightView(props) {
    let entity = CopyrightHelper.getConfig(props.config);
    return (
        <>
            <strong>
                Copyright © {entity.date}
                &nbsp;
                <a
                    href={entity.url}
                    target="_blank"
                    rel="noopener noreferrer"
                >
                    {entity.title}
                </a>
            </strong>
            &nbsp;
            All rights reserved.
        </>
    );
}
