import React from 'react';

export default function ContactTab(props) {
    return (
        <>
            <table className="table table-bordered table-striped table-">
                <tbody>
                {props.contacts.map(function (item, key) {
                    return (
                        <tr key={key}>
                            <th>{item.type}</th>
                            <td>
                                {item.url ? (
                                    <a href={item.url} target="_blank" rel="noreferrer">
                                        {item.label || item.url}
                                    </a>
                                ) : (
                                    <span>
                                        {item.label}
                                    </span>
                                )}
                            </td>
                        </tr>
                    );
                })}
                </tbody>
            </table>
        </>
    );
};
