import React from 'react';
import {Col, Container, Row} from "react-bootstrap";

import '../style/index.scss';

// import BreadcrumbsWidget from '../../../../packages/bundles/breadcrumb/web/widgets/BreadcrumbsWidget';
// import BeforeRenderWidget from "../../../../packages/ext/app/widgets/BeforeRenderWidget";
import configManager from "../../../../packages/core/configManager/singletons/configManager";
import Navigator from "../components/Navigator";
import Footer from "../components/Footer";

export default function AppLayout(props) {
    let copyrightConfig = configManager.get('copyright');
    const appTitle = configManager.get('appTitle');

    if(copyrightConfig.text == null) {
        copyrightConfig.text = 'version ' + configManager.get('appVersion');
    }

    return (
        <>
            {/*<BeforeRenderWidget/>*/}
            <Navigator className="" fixed="top" brand={appTitle}>
                {props.navigatorItems}
            </Navigator>
            {/*<Container className="mt-3">
                <Row>
                    <Col>
                        <BreadcrumbsWidget/>
                    </Col>
                </Row>
            </Container>*/}
            <Container as="main" role="main">
                {props.children}
            </Container>
            <Container>
                <Row>
                    <Col>
                        <Footer config={copyrightConfig}/>
                    </Col>
                </Row>
            </Container>
        </>
    );
}
