import container from "../../../packages/core/container/singletons/container";
import eventEmitter from "../../../packages/core/event/singletons/eventEmitter";
import AppEventEnum from '../domain/enums/AppEventEnum';
import SwitchService from "../../../packages/bundles/language/domain/services/SwitchService";
import userProviderFacade from "../../../packages/bundles/user/security/domain/singletons/userProviderFacade";

eventEmitter.emit(AppEventEnum.BEFORE_BOOTSTRAP_LOAD);

try {
    userProviderFacade.init();
} catch (e) {
}

try {
    let switchService: SwitchService = container.get('language.services.switch');
    switchService.init();
} catch (e) {
}

eventEmitter.emit(AppEventEnum.AFTER_BOOTSTRAP_LOAD);
