import BaseService from "../../../../core/domain/services/BaseService";
import ModalRepository from "../repositories/state/ModalRepository";

export default class ModalService extends BaseService {

    protected stateRepository: ModalRepository;

    constructor(stateRepository: ModalRepository) {
        super();
        this.stateRepository = stateRepository;
    }

    showByEntity(entity) {
        // this.stateRepository.setData(entity);
        this.stateRepository.show(entity);
    }

    close() {
        this.stateRepository.hide();
        // this.stateRepository.resetState();
    }
}
