import React from 'react';

export default function LinksTab(props) {
    return (
        <>
            <ul>
                {props['links'].map(function (item, key) {
                    return (
                        <li key={key}>
                            <a href={item.url} target="_blank" rel="noreferrer">
                                {item.title}
                            </a>
                        </li>
                    );
                })}
            </ul>
        </>
    );
};
