import {combineReducers, createStore} from 'redux';
import reduceConfigurator from "../../../../packages/core/state/singletons/reduceConfigurator";
import diConfigurator from "../../../../packages/core/container/singletons/diConfigurator";

let domainReducers = reduceConfigurator.getReducers();
let reducers = combineReducers(domainReducers);
let store = createStore(reducers);
diConfigurator.singleton('app.store', store);
