import React from "react";
import {Modal} from "react-bootstrap";

export default function ModalView(props) {
    let size = props.size ? props.size : 'md';
    let FooterRender = props.footerRender;
    return (
        <Modal
            size={size}
            show={props.isVisible}
            onHide={props.onHide}
        >
            {props.title ? (
                <Modal.Header closeButton>
                    <Modal.Title>
                        {props.title}
                    </Modal.Title>
                </Modal.Header>
            ) : ''}

            {props.content ?? null}

            {props.body ? (
                <Modal.Body>
                    {props.body}
                </Modal.Body>
            ) : ''}

            {props.footer ? (
                <Modal.Footer>
                    {props.footer}
                </Modal.Footer>
            ) : ''}

            {FooterRender ? (
                <FooterRender {...props} />
            ) : ''}

        </Modal>
    );
}
