import React from "react";
import SkillProgressEnum from "../../domain/enums/SkillProgressEnum";
import getColorByLevel from "../helpers/skillBadge";

export default function SkillBadge(props) {
    let level = props.level ?? SkillProgressEnum.NORMAL;
    let badgeColor = getColorByLevel(level);
    return (
        <span className={"badge badge-primary ml-1 mr-1"} style={{color: badgeColor.foregroundColor, backgroundColor: badgeColor.backgroundColor}}>
            {props.title}
        </span>
    );
}
