import React from 'react';
import CopyrightHelper from "../../../../packages/bundles/copyright/domain/helpers/CopyrightHelper";
import CopyrightView from "../../../../packages/bundles/copyright/web/views/CopyrightView";
import "../style/footer.css";

export default function Footer(props) {
    let entity = CopyrightHelper.getConfig(props.config);
    return (
        <div className="footer mt-3">
            {props.children}
            <div className="p-3 text-sm">
                <div className="d-sm-inline-block">
                    <CopyrightView config={props.config}/>
                </div>
                <div className="float-right d-sm-inline-block">
                    {entity.text}
                </div>
            </div>
        </div>
    );
}
