import React from 'react';

function dateDiffInDays(a, b) {
    const _MS_PER_DAY = 1000 * 60 * 60 * 24;
    // Discard the time and time-zone information.
    const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
    const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());

    return Math.floor((utc2 - utc1) / _MS_PER_DAY);
}

function prepareItem(item) {
    let startDate = new Date(item.dateStart.year, item.dateStart.month - 1, 1);
    let endDate = new Date(item.dateEnd.year, item.dateEnd.month - 1, 1);
    let diffDate = dateDiffInDays(startDate, endDate) + 30;
    let diffYear = Math.floor(diffDate / 365);
    let diffYearRest = diffDate % 365;
    let diffMonth = Math.floor(diffYearRest / 30);
    item['workDays'] = {
        "year": diffYear,
        "month": diffMonth
    };
    item['date'] = {
        start: new Date(item.dateStart.year, item.dateStart.month - 1, 1),
        end: new Date(item.dateEnd.year, item.dateEnd.month - 1, 1),
    };
}

export default function ExperienceTab(props) {
    let monthNames = [
        "Январь", "Февраль", "Март", "Апрель", "Май", "Июнь",
        "Июль", "Август", "Сентябрь", "Октябрь", "Ноябрь", "Декабрь"
    ];

    let companyHistory = props['companyHistory'];
    for (let key in companyHistory) {
        let item = companyHistory[key];
        prepareItem(item);
    }

    // console.log(companyHistory);

    return (
        <>
            {companyHistory.map(function (item, key) {
                let workDaysString = item.workDays.year ? (item.workDays.year + " г.") : "";
                if (item.workDays.month) {
                    workDaysString = workDaysString + " " + item.workDays.month + " м.";
                }
                let startAt = monthNames[item.date.start.getMonth()] + " " + item.date.start.getFullYear();
                let endAt = monthNames[item.date.end.getMonth()] + " " + item.date.end.getFullYear();
                return (
                    <div key={key}>

                        <small className="float-right">
                            {startAt} - {endAt} ({workDaysString})
                        </small>

                        <h3>
                            {item.title}
                        </h3>

                        <p>
                            {item.location}

                            {item.isRemote ? (
                                <>
                                    &nbsp;
                                    <small className="badge111 badge-primary111">
                                        (удаленная работа)
                                    </small>
                                </>
                            ) : null}

                            {item.website ? (
                                <>
                                    &nbsp;|&nbsp;
                                    <a
                                        href={"https://" + item.website}
                                        target="_blank"
                                        rel="noreferrer">
                                        {item.website}
                                    </a>
                                </>
                            ) : null}
                        </p>

                        <p>
                            {item.position ? (
                                <>
                                    <b>{item.position}</b>
                                </>
                            ) : null}
                        </p>

                        <p>
                            {item.description}
                        </p>

                        <hr/>

                    </div>
                );
            })}
        </>
    );
};
