import AppSlashRouter from "./AppSlashRouter";
import AppHashRouter from "./AppHashRouter";
import RouterTypeEnum from "../../../../common/app/domain/enums/RouterTypeEnum";
import configManager from "../../../core/configManager/singletons/configManager";

let AppRouter;
let routerType = configManager.get('router.type');
if(routerType === RouterTypeEnum.SLASH) {
    AppRouter = AppSlashRouter;
} else if(routerType === RouterTypeEnum.HASH) {
    AppRouter = AppHashRouter;
}

export default AppRouter;
