import BaseService from "../../../../core/domain/services/BaseService";
import ModalService from "./ModalService";
import ConfirmModalFooter from "../../web/views/confirmModalFooter";

export default class ConfirmService extends BaseService {

    protected modalService: ModalService;

    constructor(modalService: ModalService) {
        super();
        this.modalService = modalService;
    }

    show(title, body, onConfirm, onHide) {
        let modalEntity = {
            title: title,
            body: body,
            onConfirm: onConfirm,
            onHide: onHide,
            size: 'md',
            footerRender: ConfirmModalFooter,
            confirmLabel: "Delete",
        };
        this.modalService.showByEntity(modalEntity);
    }
}
