import React from "react";
import {useSelector} from "react-redux";
import ModalView from "./views/modalView";
import modalFacade from "../../domain/facades/modalFacade";
import _ from "lodash";

function ModalContainerWidget(props) {
    let confirmState = useSelector(store => store['modalItem']);
    if (!confirmState) {
        return null;
    }
    let confirmStateClone = _.clone(confirmState);
    if (confirmState.onConfirm) {
        confirmStateClone.onConfirm = () => {
            modalFacade.close();
            confirmState.onConfirm();
        };
    }
    confirmStateClone.onHide = () => {
        modalFacade.close();
        if (typeof confirmState.onHide === 'function') {
            confirmState.onHide();
        }
    };
    return (
        <ModalView {...confirmStateClone}/>
    );
}

export default React.memo(ModalContainerWidget);
