
export default function getColorByLevel(level) {
    const colorMap = {
        VERY_LOW: {
            backgroundColor: '#ff5a50',
            foregroundColor: '#a00101',
        },
        LOW: {
            backgroundColor: '#ffb228',
            foregroundColor: '#955609',
        },
        NORMAL: {
            backgroundColor: '#f9e101',
            foregroundColor: '#926c0c',
        },
        HIGH: {
            backgroundColor: '#bee551',
            foregroundColor: '#617f2c',
        },
        VERY_HIGH: {
            backgroundColor: '#81d14a',
            foregroundColor: '#307d16',
        },
    };
    return colorMap[level];
}
