import diConfigurator from "../../../../core/container/singletons/diConfigurator";
import ConfirmService from "../services/ConfirmService";
import ModalRepository from "../repositories/state/ModalRepository";
import ModalService from "../services/ModalService";

export default function configureDi(domainName: string) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.bind(
        "services.confirm",
        ConfirmService,
        [
            "this.services.modal"
        ]
    );

    bundleDiConfigurator.singleton("repositories.state.modal", ModalRepository);
    bundleDiConfigurator.bind(
        "services.modal",
        ModalService,
        [
            "this.repositories.state.modal"
        ]
    );
}
