import configManager from "../../../../core/configManager/singletons/configManager";
import CopyrightEntity from "../entities/CopyrightEntity";
import {DateTime} from "luxon";

export default class CopyrightHelper {

    public static getConfig(copyright): CopyrightEntity {
        let entity = new CopyrightEntity();
        entity.title = getTitle(copyright);
        entity.date = getDateString(copyright);
        entity.url = configManager.get('copyright.url');
        entity.text = configManager.get('copyright.text') ?? null;
        return entity;
    }
}


const getDateString = function (copyright): string {
    if (copyright.dateString) {
        return copyright.dateString;
    }
    let dateString = '';
    let dateBegin = copyright.dateBegin ?? null;
    let dateEnd = copyright.dateEnd ?? DateTime.now().toFormat('y');
    if (dateBegin) {
        dateString = dateBegin;
    }
    if (dateEnd) {
        if (dateString) {
            dateString = dateString + '-';
        }
        dateString = dateString + dateEnd;
    }
    return dateString;
};

const getTitle = function (copyright): string {
    let title = copyright.title ?? null;
    if (!title) {
        let copyrightUrl = copyright.url;
        let url = new URL(copyrightUrl);
        title = url.host;
    }
    return title;
};
